// app/blogs/[id]/page.tsx
'use client'

import { allBlogs } from '@/app/blogs/page'
import { Card, CardContent, CardDescription, CardHeader, CardTitle,CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { FiArrowLeft, FiCopy, FiTwitter, FiFacebook, FiLinkedin } from 'react-icons/fi'; // Import icons
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useState, useEffect } from 'react';
import ContactForm from '@/components/ui/contact-form';
import {motion} from "framer-motion";
import { notFound } from 'next/navigation';

interface Blog {
    id: number;
    title: string;
    date: string;
    author: string;
    description: string;
    tags: string[];
    content: string;
}
// Helper function to format date
const formatDate = (dateString: string) => {
  const [year, month, day] = dateString.split('-');
  return `${day}-${month}-${year}`;
};


export default function BlogPage({ params }: { params: { id: string } }) {

    const [currentUrl, setCurrentUrl] = useState('');

    useEffect(() => {
        // Get the current URL in the browser.  Safe for client-side rendering.
        setCurrentUrl(window.location.href);
    }, []);

    const blogId = parseInt(params.id);
    const blog = allBlogs.find((b) => b.id === blogId);

     if (!blog) {
    return notFound();
  }

    // Update the blog content here (for the first blog)
    if (blog.id === 1) {
        blog.content = `
# Building Scalable Applications

Scalability is a critical characteristic of modern applications. It refers to the ability of a system to handle a growing amount of work, or its potential to be enlarged to accommodate that growth.  A scalable application can seamlessly adapt to increasing user loads, data volumes, and overall demand without performance degradation.

## Key Principles

### 1. Horizontal Scaling (Scaling Out)

Instead of increasing the resources of a single server (vertical scaling), horizontal scaling involves adding more servers to distribute the load.  This is the preferred approach for most modern web applications because it offers better redundancy and is often more cost-effective.

### 2. Statelessness

Design your application components to be stateless. This means that any server in your cluster can handle any request, as no session data is stored on a specific server.  Statelessness makes horizontal scaling much easier.

### 3. Load Balancing

Distribute incoming network traffic across multiple servers to prevent any single server from becoming overloaded. Load balancers can be implemented in hardware or software. Tools like Nginx and HAProxy are commonly used.

### 4. Database Optimization

The database is often a bottleneck in scalable applications. Key optimization techniques include:

*   **Caching:** Store frequently accessed data in a cache (e.g., Redis, Memcached) to reduce database load.
*   **Database Sharding:** Partition your database across multiple servers to distribute the data and query load.
*   **Connection Pooling:** Reuse existing database connections instead of creating new ones for each request.
* **Read Replicas**: Use a read replica to increase database throughput.

### 5. Asynchronous Processing

Use asynchronous tasks and message queues (e.g., RabbitMQ, Kafka, SQS) to handle time-consuming operations without blocking the main application thread.  This improves responsiveness and allows the system to handle bursts of activity.

### 6. Microservices Architecture

Break down your application into smaller, independent services (microservices). Each service can be scaled independently based on its specific needs.

### 7. Content Delivery Networks (CDNs)

Use a CDN to cache static assets (images, CSS, JavaScript) closer to users, reducing latency and server load.

### 8. Monitoring and Auto-Scaling

Implement comprehensive monitoring to track application performance and resource utilization.  Use auto-scaling to automatically adjust the number of servers based on real-time demand.  Cloud providers like AWS, Google Cloud, and Azure offer robust auto-scaling capabilities.

## Example: Scaling a Web Application on AWS

1.  **EC2 Instances:** Use multiple EC2 instances (virtual servers) to run your application code.
2.  **Elastic Load Balancing (ELB):** Distribute traffic across the EC2 instances.
3.  **Auto Scaling Groups:** Automatically adjust the number of EC2 instances based on load.
4.  **RDS (Relational Database Service):** Use RDS for your database, with read replicas for increased read throughput.
5.  **ElastiCache (Redis/Memcached):** Implement caching to reduce database load.
6.  **SQS (Simple Queue Service):** Use SQS for asynchronous task processing.
7.  **CloudFront (CDN):** Cache static assets on CloudFront.
8.  **CloudWatch:** Monitor your application and infrastructure.

## Conclusion

Building scalable applications requires careful planning and attention to detail. By following these principles and leveraging cloud-based services, you can create applications that can handle massive growth and deliver a consistently positive user experience.

`;
    }

    // Get related blogs (example - by tags or latest)
    const relatedBlogs = allBlogs
        .filter((relatedBlog) => relatedBlog.id !== blog.id && relatedBlog.tags.some(tag => blog.tags.includes(tag))
        )
        .slice(0, 3); //take related blogs upto 3.




    const handleCopyUrl = () => {
        navigator.clipboard.writeText(currentUrl)
            .then(() => {
                toast.success('URL copied to clipboard!', {
                    position: "top-center",
                    autoClose: 2000,
                });
            })
            .catch((err) => {
                toast.error('Failed to copy URL.', { position: "top-center" });
                console.error('Failed to copy URL:', err);
            });
    };




    return (
        <div className="container mx-auto px-4 md:px-8 py-12">
             <ToastContainer />
            <Link href="/blogs" className="inline-flex items-center text-primary mb-4">
                <FiArrowLeft className="mr-2" /> Back to All Blogs
            </Link>
            <h1 className="text-4xl font-bold mb-4">{blog.title}</h1>
            <p className="text-muted-foreground mb-8">
                {formatDate(blog.date)} • {blog.author}
            </p>

            {/* Placeholder for blog image */}
            <div className="h-96 bg-gray-200 mb-8" />

            <div className="prose prose-lg max-w-none" dangerouslySetInnerHTML={{ __html: blog.content }}>

            </div>

            {/* Share Buttons and Copy URL */}

            <div className="flex items-center gap-4 mt-8">
                <Button variant="outline" onClick={handleCopyUrl}>
                    <FiCopy className="mr-2" /> Copy URL
                </Button>
                 Share:
                <a
                    href={`https://www.linkedin.com/shareArticle?mini=true&url=${currentUrl}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="hover:text-primary"
                >
                    <FiLinkedin className="h-6 w-6" />
                </a>
                <a
                    href={`https://twitter.com/intent/tweet?url=${currentUrl}&text=${encodeURIComponent(blog.title)}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="hover:text-primary"
                >
                    <FiTwitter className="h-6 w-6" />
                </a>
                <a
                    href={`https://www.facebook.com/sharer/sharer.php?u=${currentUrl}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="hover:text-primary"
                >
                    <FiFacebook className="h-6 w-6" />
                </a>
            </div>



            {/* Read More Blogs */}
            <section className="mt-16">
                <h2 className="text-2xl font-bold mb-6">Read More</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    {relatedBlogs.map((relatedBlog) => (
                        <Link key={relatedBlog.id} href={`/blogs/${relatedBlog.id}`}>
                        <Card className="h-full hover:shadow-lg transition-shadow cursor-pointer">
                            <CardContent className="p-0">
                                {/* Placeholder image */}
                                <div className="h-48 bg-gray-200 rounded-t-lg" />
                            </CardContent>
                            <CardHeader>
                                <CardTitle>{relatedBlog.title}</CardTitle>
                                <CardDescription>
                                    {formatDate(relatedBlog.date)} • {relatedBlog.author}
                                </CardDescription>
                            </CardHeader>
                            <CardFooter className="flex flex-wrap gap-2">
                                {relatedBlog.tags.map(tag => (
                                    <span key={tag} className="px-2 py-1 text-xs rounded-full bg-primary/10 text-primary">
                                        #{tag}
                                    </span>
                                )
                                    )}
                            </CardFooter>
                            </Card>
                        </Link>
                    ))}
                </div>
                <div className="text-center mt-8">
                    <Button asChild variant="outline">
                        <Link href="/blogs">View All Blogs</Link>
                    </Button>
                </div>
            </section>
            {/* Contact Section */}
            <section id="contact" className="py-16">
            <motion.div initial={{ opacity: 0 }} whileInView={{ opacity: 1 }} viewport={{ once: true }}>
                <h2 className="text-3xl font-bold mb-8 text-center">Contact Me</h2>
                <ContactForm />
                <div className="mt-12 text-center space-y-4">
                    <div className="flex justify-center gap-4">
                        <Button variant="outline" size="icon" asChild>
                        <a href="https://linkedin.com/in/hridaysehgal" target="_blank"> <FiLinkedin className="h-5 w-5" /> </a>
                        </Button>
                        <Button variant="outline" size="icon" asChild>
                        <a href="https://github.com/hriday-sehgal" target="_blank"> <FiGithub className="h-5 w-5" /> </a>
                        </Button>
                        <Button variant="outline" size="icon" asChild>
                        <a href="mailto:hriday.career@gmail.com"> <FiMail className="h-5 w-5" /> </a>
                        </Button> <Button variant="outline" size="icon" asChild> <a href="tel:9818045065">
                        <FiPhone className="h-5 w-5" /> </a>
                        </Button>
                    </div>
                </div>
            </motion.div>
            </section>
            {/* Footer */}
            <footer className="border-t">
            <div className="container mx-auto px-4 md:px-8 py-8">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                    <div>
                        <h3 className="text-lg font-semibold mb-4">Hriday Sehgal</h3>
                        <p className="text-muted-foreground">Building digital solutions that matter</p>
                    </div>
                    <div>
                        <h3 className="text-lg font-semibold mb-4">Quick Links</h3>
                        <div className="space-y-2">
                        <Link href="/#about" className="block text-muted-foreground hover:text-primary">About</Link> 
                        <Link  href="/#projects" className="block text-muted-foreground hover:text-primary">Projects</Link>
                        <Link href="/#blogs" className="block text-muted-foreground hover:text-primary">Blogs</Link>
                        <Link href="/#contact" className="block text-muted-foreground hover:text-primary">Contact</Link>
                        </div>
                    </div>
                    <div>
                        <h3 className="text-lg font-semibold mb-4">Connect</h3>
                        <div className="flex gap-4">
                        <Button variant="outline" size="icon" asChild>
                            <a href="https://linkedin.com/in/hridaysehgal" target="_blank"> <FiLinkedin className="h-5 w-5" /> </a>
                        </Button>
                        <Button variant="outline" size="icon" asChild>
                            <a href="https://github.com/hriday-sehgal" target="_blank">  <FiGithub className="h-5 w-5" /> </a>
                        </Button>
                        <Button variant="outline" size="icon" asChild>
                            <a href="mailto:hriday.career@gmail.com"> <FiMail className="h-5 w-5" /> </a>
                        </Button>
                        </div>
                    </div>
                </div>
                <div className="mt-8 pt-8 border-t text-center text-sm text-muted-foreground">
                    © 2025 Hriday Sehgal. All rights reserved.
                </div>
            </div>
            </footer>
        </div>
    );
}

export async function generateStaticParams() {
    return allBlogs.map((blog) => ({
        id: blog.id.toString(),
    }));
}
